body{

}

input[type=text] , input[type=number]{
    border: 1px solid gray;
    margin: 10px;
    padding: 8px;
    border-radius: 8px;
}

.json-side-container{
    background-color: #242323;
    width: 100%;
}

.yellow-text{
    color: #f2d883;
}

.purple-symbol{
    color: #b853a6;
}

.blue-symbol{
    color: #3a67e0;
}

.attribute-color{
    color: #71cede;
}

.item-color{
    color: #de814b;
}

.white-color{
    color: #FFFF;
}

button{
    background-color: #3a67e0;
    padding: 4px 12px;
    border: none;
    border-radius: 4px !important;
    color: white;
    transition: 0.3s;
}

button:hover{
    opacity: 0.7;
}